.WelcomeScreen {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background: #000080;
    font-family: 'Poppins', sans-serif;
    }
    .google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    }
    .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    cursor: pointer;
    }
    .google-btn:active {
    background: #1669f2;
    }
    .google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
    }
    .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
    }
    .btn-text {
    float: right;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
    background-color: #4285f4;
    width: 80%;
    height: 100%;
    text-align: center;
    border: 0px;
    }
    .btn-text:hover {
        cursor: pointer;
    }

#welcome-headline, #welcome-text{
    color: #f5f1f3;
}
#welcome-privacy {
    color:#4285f4;
}